/* Tło całej strony */
body {
  margin: 0;
  font-family: "Segoe UI", Arial, sans-serif;
  background-color: #eef1f5;
}

/* Główny kontener aplikacji */
.app-container {
  max-width: 900px;
  margin: 40px auto;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

/* Nagłówek */
.app-header {
  background-color: #2c3e50;
  color: #ffffff;
  padding: 25px;
  text-align: center;
}

.app-header h1 {
  margin: 0;
  font-size: 28px;
}

/* Sekcja z ćwiczeniami */
.content {
  padding: 30px;
}

/* Pojedyncze ćwiczenie */
.exercise {
  margin-bottom: 25px;
  padding: 20px;
  border-radius: 8px;
  background-color: #f9fafb;
  border-left: 6px solid #3498db;
}

/* Co drugie ćwiczenie – delikatne odróżnienie */
.exercise:nth-child(even) {
  border-left-color: #2ecc71;
  background-color: #f6fbf8;
}

.exercise h2 {
  margin-top: 0;
  color: #2c3e50;
}

/* Przyciski */
button {
  margin-top: 10px;
  padding: 8px 18px;
  background-color: #3498db;
  border: none;
  color: #ffffff;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
}

button:hover {
  background-color: #217dbb;
}

/* Inputy */
input {
  padding: 7px;
  margin: 5px 0;
  width: 260px;
  border-radius: 4px;
  border: 1px solid #ccc;
}
